.article {
  p, li, h1, h2, h3, h4, h5, h6, ul, ol {
    @include notlast {
      margin-bottom: rem(20); } }

  img {
    @include notlast {
      margin-bottom: rem(30); } }

  h1, h2, h3, h4, h5, h6 {
    @extend .title;
    @extend .title--ttn; }

  h2 {
    font-size: em(24); }

  ul {
    padding-left: 1em;
    li {
      list-style-type: disc; } }

  ol {
    @include counter('article', ')');
    li {
      &::before {
        margin-right: .3em; } } } }
